import React from "react"
import { GatsbyImage, getImage } from "gatsby-plugin-image"
import {
  Box,
  Link,
  Heading,
  Text,
} from "@chakra-ui/react"
import {graphql, Link as GatsbyLink} from "gatsby";
import Layout from "../components/Layout"
import Container from "../components/Container"
import Seo from 'gatsby-plugin-wpgraphql-seo'

const WpNewsPost = ({ data }) => {
    const { wpNewsPost } = data;
    const image = getImage(wpNewsPost.featuredImage.node.localFile)
    console.log('From Page: ', image)
    return(
        <Layout>
            <Seo post={data.wpNewsPost} />
            <Container width="100vw" centerContent>
                <Box>
                    <GatsbyImage image={image} alt={wpNewsPost.featuredImage.node.altText} />
                </Box>
                <Heading as={`h1`} m={4}>
                    {wpNewsPost.title}
                </Heading>
                <Text as={'div'} mt={4} mb={10}>
                    <div dangerouslySetInnerHTML={{ __html: wpNewsPost.content }} />
                </Text>
                <Link as={GatsbyLink} to="/">{`<< Back to Blog`}</Link>
            </Container>
        </Layout>
    )
}

export default WpNewsPost;

export const query = graphql`
query NewsById($id: String) {
  wpNewsPost(id: {eq: $id}) {
    title
    content
    seo {
        title
        metaDesc
        focuskw
        metaKeywords
        metaRobotsNoindex
        metaRobotsNofollow
        opengraphTitle
        opengraphDescription
        opengraphImage {
            altText
            sourceUrl
            srcSet
        }
        twitterTitle
        twitterDescription
        twitterImage {
            altText
            sourceUrl
            srcSet
        }
        canonical
        cornerstone
        schema {
            articleType
            pageType
            raw
        }
    }
    featuredImage {
      node {
        altText
        localFile {
          childImageSharp {
            gatsbyImageData(layout: FULL_WIDTH, placeholder: BLURRED, formats: [AUTO, WEBP, AVIF])
          }
        }
      }
    }
  }
}
`;